const GET_ANNOUNCEMENTS = `
  query GetAnnouncements {
    allAnnouncements {
      id
      name
      announcementName
      description
    }
  }
`;

function graphql(query, variables = {}) {
  return fetch('http://localhost:3000/admin/api', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      variables,
      query,
    }),
  }).then(function(result) {
    return result.json();
  });
}

import Announcement from '@/components/Page/Announcement'
import BackButton from '@/components/BackButton'

export default {
  head: {
    title: 'News',

    meta: [
      {
        name: "viewport",
        content: 'width=device-width, initial-scale=1' 
      }
    ],

    return: {
      script: [{ src: 'https://widget.cloudinary.com/v2.0/global/all.js' }]
    }
  },

  components: {
    Announcement,
    BackButton
  },

  data() {
    return {
      newAnnouncement: '',
    };
  },

  // Get the event on server side
  async asyncData() {
    const { data } = await graphql(GET_ANNOUNCEMENTS);
    return {
      announcements: data.allAnnouncements,
    };
  },

  methods: {
    async getannouncements() {
      const { data } = await graphql(GET_ANNOUNCEMENTS);
      this.announcements = data.allAnnouncements;
    },
  }
};
